body{
    background-color: azure;
}
.container{
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
.image{
    -ms-flex-pack: center;
        justify-content: center;
    width: 370px;
    margin-top: 30px;
    -ms-flex-item-align: center;
        align-self: center;
    margin-bottom: 30px;
    padding-left: 8%;
}

@media (max-width:770px){
    .container{
        -ms-flex-pack: center;
            justify-content: center;
        padding-left: 7%;
    }
    .image{
        width: 80%;
    }
}


